import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserActivityService } from '../../../../common/user/useractivity.service';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';

@Component({
  selector: 'cart-main',
  templateUrl: 'cart-main.component.html',
  styleUrls: ['./cart-main.component.css']
})
export class CartMainComponent implements OnInit, OnDestroy {

  alive = true;

  private menuList: any[];

  maxStep = 0;

  numberVoucherCart = 0;
  productsCart;

  constructor(private useractivityService: UserActivityService, private router: Router) {

    this.useractivityService.cartChanged.takeWhile(() => this.alive).subscribe(
      (changedCart: boolean) => {
        if (changedCart) {
          this.getProductsCart();
        }
      }
    );
    this.menuList = [
      { title: "Summary", path: "summary" },
      { title: "Payment Method", path: "payment" },
      { title: "Shipping", path: "shipping" },
      { title: "Confirmation", path: "confirmation" },
    ]
  }

  ngOnInit() {

    this.getProductsCart();
    this.validateCurrentStep(this.router.url);
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationStart) {
        this.validateCurrentStep(event.url);
      }
    });
  }
  validateCurrentStep(url) {
    let firsStep = this.maxStep === 0;
    if (url.indexOf("payment") > -1 && this.maxStep <= 1) {
      this.maxStep = 1;
    } else if (url.indexOf("shipping") > -1 && this.maxStep <= 2) {
      this.maxStep = 2;
    } else if (url.indexOf("confirmation") > -1 && this.maxStep <= 3) {
      this.maxStep = 3;
    }
    if (firsStep && this.maxStep > 1) {
      this.maxStep = 0;
      this.router.navigate(["/store/cart"]);
    }
  }
  stepEnabled(item) {
    let iteIndex: any = 0;
    for (const iteI in this.menuList) {
      let ite = this.menuList[iteI];
      if (item.path === ite.path) {
        iteIndex = iteI;
        break;
      }
    }
    return iteIndex <= this.maxStep;

  }

  getProductsCart() {
    this.productsCart = {};
    this.useractivityService.getShoppingCart(false).takeWhile(() => this.alive).subscribe(
      res => {
        this.productsCart = res;
        this.numberVoucherCart = this.productsCart.grandTotalVouchers;
      },
      error => {
        console.log('Error occured' + error);
      }
    );
  }

  ngOnDestroy(): void {
    this.alive = false;
  }
}



