import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserActivityService } from '../../../../common/user/useractivity.service';
import { TradeService } from '../../../../common/trade/trade.service';
import { VoucherService } from '../../../../common/voucher/voucher.service';
import { MediaService } from 'app/common/media/media.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cart-summary',
  templateUrl: './cart-summary.component.html',
  styleUrls: ['./cart-summary.component.css']
})
export class CartSummaryComponent implements OnInit, OnDestroy {

  cartItems: any;
  cartTotal: number;
  previousPurchases: any[];
  private recomendations: any = [];
  alive = true;
  private currentPage = 0;
  private pageElements = 9;
  private totalProducts = 0;
  private currentPageP = 0;
  private pageElementsP = 9;
  private totalProductsP = 0;
  private sortType = 1;

  constructor(private useractivityService: UserActivityService, private tradeService: TradeService, private voucherService: VoucherService,
    private mediaService: MediaService,
    private router: Router,
    private sanitizer: DomSanitizer, ) {
  }

  ngOnInit() {
    this.getCartItems();
    this.getRecommendations();
    this.getPreviousPurchases();

    this.useractivityService.cartChanged.takeWhile(() => this.alive).subscribe(
      (changedCart: boolean) => {
        this.getCartItems();
      }
    );
  }

  searchRecomendations() {

  }

  changeQuantity(voucher) {
    this.useractivityService.manageCart(voucher.voucherVendorLocationCode, voucher.quantity).takeWhile(() => this.alive).subscribe(
      result => {
        this.getCartItems();
      },
      error => { console.log('Error occured', error); });
  }
  addQuantity(voucher) {
    voucher.quantity++;
    this.useractivityService.manageCart(voucher.voucherVendorLocationCode, 1).takeWhile(() => this.alive).subscribe(
      result => {
        this.getCartItems();
      },
      error => { console.log('Error occured', error); });
  }
  remQuantity(voucher) {
    voucher.quantity--;
    if (voucher.quantity < 1) {
      return;
    }
    this.useractivityService.manageCart(voucher.voucherVendorLocationCode, -1).takeWhile(() => this.alive).subscribe(
      result => {
        this.getCartItems();
      },
      error => { console.log('Error occured', error); });
  }

  getCartItems() {

    this.useractivityService.getShoppingCart(false).takeWhile(() => this.alive).subscribe(
      res => {
        console.log(res);
        this.cartItems = res;
        this.cartTotal = this.cartItems.grandTotalVouchers;
        for (const voucher of this.cartItems.vouchers) {
          if (!voucher || !voucher.media[0] || !voucher.media[0].mediaUrl) {
            return;
          }
          this.mediaService.getMedia(voucher.media[0].mediaUrl).takeWhile(() => this.alive).subscribe(
            result => {
              voucher.image = this.sanitizer.bypassSecurityTrustResourceUrl(result);
            }
          );
        }
      },
      error => {
        this.cartItems = {};
        console.log('Error occured' + error);
      }
    );
  }

  goToProduct(product) {
    window.scrollTo(0, 0);
    this.router.navigate(["/voucher/details-voucher/", product.voucherCode]);
  }

  deleteItem(voucher) {
    //if(Context.token){

    this.useractivityService.manageCart(voucher.voucherVendorLocationCode, (voucher.quantity * -1) - 1).takeWhile(() => this.alive).subscribe(
      result => {
        this.getCartItems();
      },
      error => { console.log('Error occured', error); });

  }


  saveItemForLater(index: number) {
    // change to service
    this.cartItems.splice(index, 1);
  }

  getRecommendations() {
    // this.tradeService.getTrades("4", "4", 1, 5, this.sortType).takeWhile(() => this.alive).subscribe((res) => {
    //   this.recomendations = res;
    //
    // })
    this.voucherService.getVouchers(null, '4', this.currentPage, this.pageElements, 1).subscribe(
      res => {
        // if (res && res[0] && res[0].totalCount) {
        //   this.totalProducts = res[0].totalCount;
        // }
        for (var i in res) {
          this.voucherService.getVoucherDetail(res[i].voucherVendorLocationCode).takeWhile(() => this.alive).subscribe(
            res => {
              this.recomendations.push(res);
            });
        }
      });
  }

  getPreviousPurchases() {
    // change to service
    this.tradeService.getTrades("4", "4", this.currentPageP, this.pageElementsP, this.sortType).takeWhile(() => this.alive).subscribe((res) => {
      console.log("prev>>> ", res);
      this.previousPurchases = res;
    })
  }

  counter(length) {
    return new Array(length);
  }
  ngOnDestroy(): void {
    this.alive = false;
  }
}



