import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CartMainComponent } from './cart-main/cart-main.component';
import { CartSummaryComponent } from './cart-summary/cart-summary.component';
import { CartConfirmationComponent } from './cart-confirmation/cart-confirmation.component';
import { CartPaymentMethodComponent } from './cart-payment-method/cart-payment-method.component';
import { CartReviewComponent } from './cart-review/cart-review.component';
import { CartShippingComponent } from './cart-shipping/cart-shipping.component';
import { CartDoneComponent } from './cart-done/cart-done.component';

const storeRoutes: Routes = [
  {
    path: '', component: CartMainComponent, children: [
      { path: '', redirectTo: 'summary', pathMatch: 'full' },
      { path: 'summary', component: CartSummaryComponent },
      { path: 'shipping', component: CartShippingComponent },
      { path: 'confirmation', component: CartConfirmationComponent },
      { path: 'payment', component: CartPaymentMethodComponent },
      { path: 'review', component: CartReviewComponent },
      { path: 'done', component: CartDoneComponent },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(storeRoutes)],
  exports: [RouterModule]
})
export class CartRoutingModule { }



