import { Component, OnInit } from '@angular/core';
import { StripeCheckoutLoader, StripeCheckoutHandler, IStripeCheckoutConfig } from 'ng-stripe-checkout';
import { UserActivityService } from '../../../../common/user/useractivity.service';
import { UserprofileService } from '../../../../common/user/userprofile.service';
import { UserService } from '../../../../common/user/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cart-confirmation',
  templateUrl: './cart-confirmation.component.html',
  styleUrls: ['./cart-confirmation.component.css']
})
export class CartConfirmationComponent implements OnInit {

  cartItems: any;
  cartTotal: number;
  alive: boolean = true;
  shipping: any;
  billing: any;
  user: any;
  currentDate = new Date();
  isCreditCard: boolean = false;
  errorCheckout: boolean = false;

  checkoutDone: boolean = false;

  private stripeCheckoutHandler: StripeCheckoutHandler;
  constructor(private stripeCheckoutLoader: StripeCheckoutLoader,
    private userprofileService: UserprofileService,
    private userService: UserService,
    private router: Router,
    private useractivityService: UserActivityService) { }


  getShippingAddresses() {
    // this.userprofileService.getShippingAddresses()
    //   .takeWhile(() => this.alive).subscribe((resp) => {
    //     this.addresses = resp;
    //     console.log("this.addresses>>> ", this.addresses);
    //   })
    this.userprofileService.getUserProfile()
      .takeWhile(() => this.alive).subscribe((resp) => {
        console.log("getUserProfile>>> ", resp.shippingAddresses);
        this.shipping = {
          fullName: resp.shippingFullName,
          address1: resp.shippingAddress1,
          address2: resp.shippingAddress2,
          city: resp.shippingCity,
          province: resp.shippingProvince,
          zipOrPostalCode: resp.shippingZipOrPostalCode,
          phoneNumber: resp.shippingPhoneNumber,
        };
        this.billing = {
          fullName: resp.billingFullName,
          address1: resp.billingAddress1,
          address2: resp.billingAddress2,
          city: resp.billingCity,
          province: resp.billingProvince,
          zipOrPostalCode: resp.billingZipOrPostalCode,
          phoneNumber: resp.billingPhoneNumber,
        };
      })
  }

  currentUser() {
    this.userService.getUser().takeWhile(() => this.alive).subscribe((user) => {
      this.user = user;
    });
    this.useractivityService.getSources().takeWhile(() => this.alive).subscribe((user) => {
      console.log("SOURCES", user);
    });
  }
  continue(){
    this.router.navigate(["/"])
  }

  checkout() {
    this.useractivityService.checkout("4", true).subscribe(() => {
      this.useractivityService.cartChanged.next(true);
    }, () => {
      this.errorCheckout = true;
    });
  }

  ngOnInit() {
    this.currentDate.setDate(this.currentDate.getDate()+2);
    this.currentUser();
    this.getShippingAddresses();
    this.stripeCheckoutLoader.createHandler({
      key: 'pk_test_XkEqeYkXhH2GnE8M1qpPMoFJ',
      token: (token) => {
        this.checkoutDone = true;
        this.useractivityService.addSource(this.shipping.fullName, this.shipping.fullName, token.id, this.shipping.phoneNumber).subscribe(() => {
          this.checkout();
        }, () => {
          this.checkout();
        })
        // Do something with the token...
        console.log('Payment successful!', token);
      },
    }).then((handler: StripeCheckoutHandler) => {
      this.stripeCheckoutHandler = handler;
    });
    this.getCartItems();
  }

  getCartItems() {
    this.cartItems = {};
    this.useractivityService.getShoppingCart(false).takeWhile(() => this.alive).subscribe(
      res => {
        console.log(res);
        this.cartItems = res;
        this.cartTotal = this.cartItems.grandTotalVouchers;
      },
      error => {
        console.log('Error occured' + error);
      }
    );
  }

  public onClickBuy() {
    if (!this.cartItems || this.cartItems.grandTotalVouchers < 1) {
      alert("invalid items");
      return;
    }
    if (!this.cartItems || this.cartItems.grandTotal < 0) {
      alert("invalid Total");
      return;
    }

    let description = this.cartItems.vouchers.map((vo) => vo.shortDescription).join(" - ");
    this.errorCheckout = false;
    this.stripeCheckoutHandler.open({
      amount: this.cartItems.grandTotal * 100,
      image: "/assets/images/logo_big.png",
      name: "Panda Weed",
      description: description,
      email: this.user.userName,
      currency: 'USD',
    });
  }

  public onClickCancel() {
    // If the window has been opened, this is how you can close it:
    this.stripeCheckoutHandler.close();
  }

}



