import { Pipe, PipeTransform } from '@angular/core';

// use: {{ array | sum:'attrToSum' }}
@Pipe({
  name: 'sum'
})
export class SumPipe implements PipeTransform {
  transform(items: any[], attr: string): any {
    return items.reduce((a, b) => a + b[attr], 0);
  }
}



