import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FlexLayoutModule } from '@angular/flex-layout';
import { CardModule } from '../../../common/components/card';
import { SumPipe } from '../../../common/pipes/sum.pipe';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { NglModule } from 'ng-lightning/ng-lightning';
import { VoucherCardModule } from '../../../common/components/voucher';

import { CartRoutingModule } from './cart.routing';
import { CartMainComponent } from './cart-main/cart-main.component';
import { CartSummaryComponent } from './cart-summary/cart-summary.component';
import { CartConfirmationComponent } from './cart-confirmation/cart-confirmation.component';
import { CartPaymentMethodComponent } from './cart-payment-method/cart-payment-method.component';
import { CartPaymentMethodCreditComponent } from './cart-payment-method-credit/cart-payment-method-credit.component';
import { CartReviewComponent } from './cart-review/cart-review.component';
import { CartShippingComponent } from './cart-shipping/cart-shipping.component';
import { CartDoneComponent } from './cart-done/cart-done.component';
import { StripeCheckoutModule } from 'ng-stripe-checkout';

@NgModule({
  imports: [
    CommonModule,
    CartRoutingModule,
    FlexLayoutModule,
    CardModule,
    ReactiveFormsModule,
    FormsModule,
    NglModule,
    VoucherCardModule,
    StripeCheckoutModule
  ],
  declarations: [
    SumPipe,
    CartMainComponent,
    CartPaymentMethodCreditComponent,
    CartSummaryComponent,
    CartConfirmationComponent,
    CartPaymentMethodComponent,
    CartReviewComponent,
    CartShippingComponent,
    CartDoneComponent
  ]
})
export class CartModule { }



