import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { UserprofileService } from '../../../../common/user/userprofile.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cart-payment-method',
  templateUrl: './cart-payment-method.component.html',
  styleUrls: ['./cart-payment-method.component.css']
})
export class CartPaymentMethodComponent implements OnInit {

  alive: boolean = true;
  address: any;
  addressForm: FormGroup;
  addresses: any = [];

  constructor(private formBuilder: FormBuilder, private userprofileService: UserprofileService, private router: Router) {
    this.addressForm = formBuilder.group({
      'fullName': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(50)])],
      'address1': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(50)])],
      'address2': [null],
      'city': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(50)])],
      'province': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(50)])],
      'zipOrPostalCode': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(50)])],
      'phoneNumber': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(50)])],
    });
  }

  ngOnInit() {
    this.getBillingAddresses();
  }

  ngOnDestroy() {
    this.alive = false;
  }

  addBillingAddress() {
    this.addresses.push(this.addressForm.value);
    console.log(">>> ", JSON.stringify(this.addresses));
    this.saveBillingAddresses();
  }

  getBillingAddresses() {
    this.userprofileService.getUserProfile()
      .takeWhile(() => this.alive).subscribe((resp) => {
        console.log("resp", resp);
        this.addressForm.patchValue({
          fullName: resp.billingFullName,
          address1: resp.billingAddress1,
          address2: resp.billingAddress2,
          city: resp.billingCity,
          province: resp.billingProvince,
          zipOrPostalCode: resp.billingZipOrPostalCode,
          phoneNumber: resp.billingPhoneNumber,
        });

      })
  }

  saveBillingAddresses() {
    this.userprofileService.setUserProfile([
      { key: "billingFullName", value: this.addressForm.value.fullName },
      { key: "billingAddress1", value: this.addressForm.value.address1 },
      { key: "billingAddress2", value: this.addressForm.value.address2 },
      { key: "billingCity", value: this.addressForm.value.city },
      { key: "billingProvince", value: this.addressForm.value.province },
      { key: "billingZipOrPostalCode", value: this.addressForm.value.zipOrPostalCode },
      { key: "billingPhoneNumber", value: this.addressForm.value.phoneNumber },
    ])
      .takeWhile(() => this.alive).subscribe((resp) => {
        this.router.navigate(['/store/cart/shipping']);
      })
  }

  deleteBillingAddress(index: number) {
    this.addresses.splice(index, 1);
    this.saveBillingAddresses();
  }

  editBillingAddress(index: number) {
    this.addressForm.setValue(this.addresses[index]);
  }
}



